import { useWeb3React } from "@web3-react/core";
import cx from "classnames";
import useSWR from "swr";
import React, { useState, useEffect } from "react";
import { ethers } from "ethers";

import SectionTitle from "../SectionTitle";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import TableDecoImg from "assets/images/staking/tableDeco.png";
import CircularArrowImg from "assets/images/common/Arrow.png";
import XImage from "assets/images/common/XLight.png";
import RoundButton from "../RoundButton";
import { getContract } from "config/contracts";
import { LOH_NFTS, REWARD_AMOUNTS, VOLUME1_METADATA, COLLECTION_OPTIONS } from "config/staking";
import { callContract, contractFetcher } from "lib/contracts";
import { useChainId } from "lib/chains";
import { approveNFT } from "lib/approveNFT";
import BurningLOH from "abis/BurningLOH.json";
import LOHStakingCard from "./LOHStakingCard";
import Combobox from './Combobox';
import Description from "components/Description";

const timeTypeInfo = [
  {
    label: "7-day",
    id: "seven",
    period: 7,
  },
  {
    label: "30-day",
    id: "thirty",
    period: 30,
  },
  {
    label: "60-day",
    id: "sixty",
    period: 60,
  },
  {
    label: "90-day",
    id: "ninety",
    period: 90,
  },
];

const getRarity = (collection, tokenId) => {
  if (collection.toLowerCase() === "0x770fa15c43b84f61434321f5167814b64790e6fa")
    return 4;

  if (tokenId >= 1 && tokenId <= 150) return 3;
  else if (tokenId >= 151 && tokenId <= 450) return 2;
  else if (tokenId >= 451 && tokenId <= 900) return 1;
  else if (tokenId >= 901 && tokenId <= 3000) return 0;

  return 0;
};

const loadWalletNFTs = (dataWallet, timeType) => {
  if (dataWallet === undefined) return [];

  if (dataWallet.length === 0) return [];

  const ret = dataWallet.map((item) => {
    let index = -1;

    for (let i = 0; i < LOH_NFTS.length; i++) {
      if (
        LOH_NFTS[i].address.toLowerCase() === item.token_address.toLowerCase()
      ) {
        index = i;
        break;
      }
    }

    if (index === -1) return null;

    const nftInfo = LOH_NFTS[index];
    let imageURL = "";
    if (nftInfo.name === "LIFE OF HEL - Volume 1")
      imageURL = `${nftInfo.imageHash}${VOLUME1_METADATA[Number(item.token_id.toString()) - 1]
        }.${nftInfo.imageType}`;
    else
      imageURL = `${nftInfo.imageHash}${Number(item.token_id.toString())}.${nftInfo.imageType
        }`;

    const rarity = getRarity(nftInfo.address, item.token_id.toString());
    const rewards = REWARD_AMOUNTS[rarity][timeType];
    return {
      id: item.token_id.toString(),
      name: item.name,
      address: nftInfo.address,
      image: imageURL,
      rewards: rewards,
      pointAmount: nftInfo.pointAmount,
    };
  });

  const finalArray = ret.filter((item) => item !== null);
  return finalArray;
};

let walletNFTSelection = [];

const LOHStaking = ({ isMobile }) => {
  const { active, library, account } = useWeb3React();
  const [currentTimeType, setCurrentTimeType] = useState(0);
  const [selectStatusInWallet, setSelectStatusInWallet] = useState([]);
  const [isApproving, setIsApproving] = useState(false);
  const [isWaitingForApproval, setIsWaitingForApproval] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [selectedCollection, setSelectedCollection] = useState(COLLECTION_OPTIONS[0]);

  const regInputForCollectionId = React.useRef();

  const { chainId } = useChainId();
  const burningLOHAddress = getContract(chainId, "BurningLOH");

  const nftAddresses = LOH_NFTS.map((item) => item.address);
  //////////Reading Wallet///////////////////////////////////////////////////////////////////////////////////////////////////
  const { data: dataWallet } = useSWR(
    active &&
    burningLOHAddress && [
      active,
      chainId,
      burningLOHAddress,
      "getTokensInWallet",
      nftAddresses,
    ],
    { fetcher: contractFetcher(library, BurningLOH) }
  );
  const filteredDataWallet = dataWallet ? dataWallet.filter((item) => item.token_address.toLowerCase() === selectedCollection.address.toLowerCase()) : [];
  let nftsInWallet = loadWalletNFTs(filteredDataWallet, currentTimeType);

  /////////Approval Status/////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  const { data: collectionApprovals } = useSWR(
    active &&
    burningLOHAddress && [
      active,
      chainId,
      burningLOHAddress,
      "getApprovalStatus",
      nftAddresses,
      account,
    ],
    { fetcher: contractFetcher(library, BurningLOH) }
  );

  let needApproval = true;
  if (collectionApprovals) {
    const filteredCollectionApprovals = collectionApprovals ? collectionApprovals.filter((item) => item.token_address.toLowerCase() === selectedCollection.address.toLowerCase()) : [];
    needApproval = !filteredCollectionApprovals[0].isApproval;
  }

  const [disabledBurnBtn, setDisabledBurnBtn] = useState(!needApproval);

  ///////////////////////////////////////////////////////////////////////////////////////////////////////////////
  const handleSelectNFTInWallet = (index) => {
    const itemInfo = nftsInWallet[index];

    const selectedItem = walletNFTSelection.find(
      (item) =>
        item.id === itemInfo.id &&
        item.address === itemInfo.address.toLowerCase()
    );
    if (selectedItem === undefined)
      walletNFTSelection.push({
        id: itemInfo.id,
        address: itemInfo.address.toLowerCase(),
      });
    else
      walletNFTSelection = walletNFTSelection.filter(
        (item) => item !== selectedItem
      );

    let items = [];
    for (let i = 0; i < nftsInWallet.length; i++) {
      const itemInfo = nftsInWallet[i];
      const currentItem = walletNFTSelection.find(
        (item) =>
          item.id === itemInfo.id &&
          item.address === itemInfo.address.toLowerCase()
      );
      if (currentItem === undefined) items.push(false);
      else items.push(true);
    }
    setSelectStatusInWallet(items);

    if (walletNFTSelection.length === 0 && !needApproval)
      setDisabledBurnBtn(true);
    else setDisabledBurnBtn(false);
  };

  const handleSelectAllInWallet = () => {
    let items = [];
    walletNFTSelection = [];
    for (let i = 0; i < nftsInWallet.length; i++) {
      const itemInfo = nftsInWallet[i];
      walletNFTSelection.push({
        id: itemInfo.id,
        address: itemInfo.address.toLowerCase(),
      });
      items.push(true);
    }
    setSelectStatusInWallet(items);

    setDisabledBurnBtn(false);
  };

  const handleUnselectAllInWallet = () => {
    let items = [];
    walletNFTSelection = [];
    setSelectStatusInWallet(items);
    setDisabledBurnBtn(true);
  };

  const handleTimeType = (index) => {
    setCurrentTimeType(index);
  };

  const approveFromNFT = () => {
    if (needApproval) {
      approveNFT({
        setIsApproving,
        library,
        tokenAddress: selectedCollection.address,
        spender: burningLOHAddress,
        chainId: chainId,
        onApproveSubmitted: () => {
          setIsWaitingForApproval(true);
        },
      });

      return;
    }
  };

  const handleBurn = () => {
    if (needApproval) {
      approveFromNFT();
      return;
    }

    setIsSubmitting(true);

    const nftToBurn = walletNFTSelection.map((item) => {
      return item.address;
    });

    const tokenId = walletNFTSelection.map((item) => {
      return item.id;
    });

    const contract = new ethers.Contract(
      burningLOHAddress,
      BurningLOH,
      library.getSigner()
    );
    callContract(chainId, contract, "burn", [nftToBurn, tokenId], {
      sentMsg: "Burning LOH NFT submitted!",
      failMsg: "Burning LOH NFT failed.",
      // setPendingTxns,
    })
      .then(async (res) => {
        // setIsVisible(false);
        // setOpenBuyModal(true);
        // setOpenBoughtModal(false);
      })
      .finally(() => {
        // setIsStaking(false);
        // setOpenBuyModal(false);
        // setOpenBoughtModal(true);
        setIsSubmitting(false);
      });

    handleUnselectAllInWallet();
  };

  const getTextBurn = () => {
    // const [error, modal] = getErrorForDeposit();

    // if (error && !modal) {
    //   return error;
    // }

    if (needApproval && isWaitingForApproval) {
      return "Waiting for Approval...";
    }
    if (isApproving) {
      return "Approving";
    }
    if (needApproval) {
      return "Approve";
    }

    if (isSubmitting) {
      return "Burning...";
    }

    return "Burn";
  };

  useEffect(() => {
    if (needApproval) setIsWaitingForApproval(false);
  }, [needApproval, active]);

  useEffect(() => {
    handleUnselectAllInWallet();
  }, [selectedCollection]);

  return (
    <div className={cx("loh-staking")}>
      <SectionTitle classes="mb-3">
        LOH <span>burn</span>
      </SectionTitle>
      <Description classes={cx("mb-3")} >Select your LOH NFTs that you would like to burn</Description>
      <Combobox ref={regInputForCollectionId} options={COLLECTION_OPTIONS} value={selectedCollection} onChange={(selectedOption) => setSelectedCollection(selectedOption)} />
      <Description classes={cx("inyourwallet")}>
        In your wallet:
      </Description>

      <div className="loh-burning-select">
        <p>
          <span>{walletNFTSelection ? walletNFTSelection.length : 0}</span> NFTS SELECTED
        </p>
        <p>
          <span>{walletNFTSelection ? walletNFTSelection.length * selectedCollection.pointAmount : 0}</span> LOH POINTS REDEEMABLE
        </p>
      </div>
      <div className={cx("loh-staking-tabs")}>
        {/* <TabList>
          <Tab>
            <p>
              <span>{nftsInWallet ? nftsInWallet.length : 0}</span> in wallet
            </p>
          </Tab>
          <Tab>
            <p>
              <span>{nftsStaked ? nftsStaked.length : 0}</span> staked
            </p>
          </Tab>
        </TabList> */}

        {isMobile ? (
          <div className={cx("cards-wrapper", "mb-5")}>
            {nftsInWallet && nftsInWallet.length ? (
              nftsInWallet.map((item, index) => {
                return (
                  <LOHStakingCard
                    nft={item}
                    key={index}
                    timeTypeInfo={timeTypeInfo}
                    currentTimeType={currentTimeType}
                    index={index}
                    selectStatusInWallet={selectStatusInWallet}
                    handleSelectNFTInWallet={handleSelectNFTInWallet}
                  />
                );
              })
            ) : (
              <div className={cx("p-1", "text-center", "text-danger")}>
                No NFTs in wallet
              </div>
            )}
          </div>
        ) : (
          <div className={cx("table-wrapper", "mb-5")}>
            <table>
              <thead>
                <tr rowSpan={nftsInWallet ? nftsInWallet.length + 1 : 0}>
                  <th>nft visual</th>
                  <th>nft #</th>
                  <th>selected</th>
                  <th>burning point</th>
                </tr>
              </thead>
              <tbody>
                {nftsInWallet && nftsInWallet.length ? (
                  nftsInWallet.map((item, index) => {
                    return (
                      <tr key={item.id}>
                        <td className="nft-visual">
                          <img src={item.image} alt={`${item.image}`} />
                        </td>
                        <td className="w-20">
                          {item.name} #{item.id}
                        </td>
                        <td>
                          <div className="loh-checkbox">
                            <input
                              type="checkbox"
                              id={`lohCheckbox-${index}`}
                              checked={selectStatusInWallet[index] === true}
                              onChange={() => handleSelectNFTInWallet(index)}
                              value={selectStatusInWallet[index]}
                            />
                            <label htmlFor={`lohCheckbox-${index}`}></label>
                          </div>
                        </td>
                        <td className="days">
                          <span className="text-primary">
                            {item.pointAmount}
                          </span>
                        </td>
                      </tr>
                    );
                  })
                ) : (
                  <tr>
                    <td
                      colSpan={6}
                      className={cx("text-danger", "text-center", "p-1")}
                    >
                      No NFTs in wallet
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        )}
        <div className="btn-group">
          <button
            className={cx("select-all-btn")}
            onClick={handleSelectAllInWallet}
          >
            select all
          </button>
          <RoundButton
            key="burn"
            text={getTextBurn()}
            variant="primary"
            disabled={disabledBurnBtn}
            onBtnClick={handleBurn}
          />
        </div>
      </div>
    </div>
  );
};

export default LOHStaking;
