import { useWeb3React } from "@web3-react/core";
import useSWR from "swr";
import Layout from "components/Layout";
import Hero from "components/Burn/Hero";
import LOHStaking from "components/Burn/LOHBurning";
import StakingContainer from "components/Burn/StakingContainer";
import { useMediaQuery } from "@uidotdev/usehooks";
import { getContract } from "config/contracts";
import { useChainId } from "lib/chains";
import { contractFetcher } from "lib/contracts";
import BurningLOH from "abis/BurningLOH.json";

import "./index.scss";

// ================================================

const Burn = (props) => {
  const { connectWallet } = props;

  const { active, library, account } = useWeb3React();
  const isMobile = useMediaQuery("only screen and (max-width: 640px)");

  const { chainId } = useChainId();
  const burningLOHAddress = getContract(chainId, "BurningLOH");

  const { data: startTimeData } = useSWR(
    true &&
    burningLOHAddress && [
      true,
      chainId,
      burningLOHAddress,
      "startTime",
    ],
    { fetcher: contractFetcher(library, BurningLOH) }
  );

  const startTime = startTimeData ? parseInt(startTimeData.toString()) : 0;

  const { data: endTimeData } = useSWR(
    true &&
    burningLOHAddress && [
      true,
      chainId,
      burningLOHAddress,
      "getEndTime",
    ],
    { fetcher: contractFetcher(library, BurningLOH) }
  );

  const endTime = endTimeData ? parseInt(endTimeData.toString()) : 0;
  const currentTime = new Date().getTime() / 1000;

  let burningStatus = 1;
  if (currentTime < startTime) // Not started
    burningStatus = 0;
  else if (currentTime >= startTime && currentTime <= endTime) // Burning
    burningStatus = 1;
  else if (currentTime > endTime) // Ended
    burningStatus = 2;

  return (
    <Layout>
      <StakingContainer>
        {(!active || burningStatus !== 1) ? (
          <>
            <Hero connectWallet={connectWallet} burningStatus={burningStatus} endTime={endTime} />
          </>
        ) : (
          <LOHStaking isMobile={isMobile} />
        )}
      </StakingContainer>
    </Layout>
  );
};

export default Burn;
